<template>
<section class="myself-container content">
    <div class="top_title">资源监控</div>
    <div class="user-title" style="display: flex;flex-flow: column;">
        <div class="resources_con">
            <el-tabs v-model="activeName" @tab-click="handleClick">
                <el-tab-pane label="加速流量" name="first">
                    <div style="display: flex;flex-flow: row;margin-top: 20px;padding:20px 37px;background:rgba(255,255,255,1);box-shadow:0px 2px 3px 0px rgba(6,17,36,0.14);border-radius:2px;">
                        <el-select v-model="value1Active" placeholder="渠道ID" style="width: 10%;margin-right: 10px;" @change="getdata()">
                            <el-option label="全部" value="*"></el-option>
                            <el-option v-for="(item, index) in options1Active" :key="index" :label="item.label" :value="item.label"></el-option>
                        </el-select>
                        <el-select v-model="value1" placeholder="文件名" style="width: 10%;margin-right: 10px;" @change="getdata()">
                            <el-option label="全部" value="*"></el-option>
                            <el-option v-for="(item, index) in options1" :key="index" :label="item.label" :value="item.label"></el-option>
                        </el-select>
                        <el-select v-model="value2" placeholder="区域" style="width: 10%;margin-right: 10px;" @change="getdata()">
                            <el-option label="全部" value="*"></el-option>
                            <el-option v-for="(item) in options2" :key="item.value" :label="item.label" :value="item.label"></el-option>
                        </el-select>
                        <el-select v-model="value3" placeholder="运营商" style="width: 10%;margin-right: 10px;" @change="getdata()">
                            <el-option label="全部" value="*"></el-option>
                            <el-option v-for="(item, index) in options3" :key="item + index" :label="item.label" :value="item.label"></el-option>
                        </el-select>
                        <el-button-group>
                            <el-button v-show="!shoudzyx" @click="today()">今天</el-button>
                            <el-button v-show="!shoudzyx" @click="yesterday()">昨天</el-button>
                            <el-button v-show="!shoudzyx" @click="sevendat()">近7天</el-button>
                            <el-button v-show="!shoudzyx" @click="thirtyday()">近30天</el-button>
                            <el-button @click="showzdyx">自定义<i class="el-icon-date"></i></el-button>
                        </el-button-group>
                        <el-date-picker v-show="shoudzyx" style="margin-left:10px;" v-model="val2" type="datetimerange" :picker-options="pickerOptions" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" align="left" @change="gettimes"></el-date-picker>
                        <el-button style="margin-left:10px;" type="primary" @click="seachtu(1)">确定</el-button>
                    </div>
                    <div class="device_form">
                        <div id="myChart" :style="{ height: '607px' }"></div>
                    </div>
                    <div class="devide_table">
                        <el-row type="flex" class="row_active">
                            <el-col :span="24" style="text-align:left;font-weight: bold;padding-left: 10px;">IP流量平均利用率表</el-col>
                        </el-row>
                        <el-row type="flex" class="row_active">
                            <el-col :span="24">
                                <el-table :data="tablecdn" border style="width: 98%;margin:10px;" :cell-style="rowClass" :header-cell-style="headClass">
                                    <el-table-column label="时间">
                                        <template slot-scope="scope">
                                            <div>{{ scope.row.timeStamp | settimes }}</div>
                                        </template></el-table-column>
                                    <el-table-column label="总流量">
                                        <template slot-scope="scope">
                                            <div>{{ scope.row.dataFlow }}</div>
                                        </template>
                                    </el-table-column>
                                </el-table>
                                <fenye2 style="float:right;margin:10px 0 20px 0;" @fatherMethod="getpage" @fathernum="gettol" :pagesa="total_cnt"></fenye2>
                            </el-col>
                        </el-row>
                    </div>
                </el-tab-pane>

                <el-tab-pane label="回源统计" name="second">
                    <div style="display: flex;flex-flow: row;margin-top: 20px;padding:20px 37px;background:rgba(255,255,255,1);box-shadow:0px 2px 3px 0px rgba(6,17,36,0.14);border-radius:2px;">
                       <el-select v-model="value1Active" placeholder="渠道ID" style="width: 10%;margin-right: 10px;" @change="getdata1()">
                            <el-option label="全部" value="*"></el-option>
                            <el-option v-for="(item, index) in options1Active" :key="index" :label="item.label" :value="item.label"></el-option>
                        </el-select>
                        <el-select v-model="valuea2" placeholder="区域" style="width: 10%;margin-right: 10px;" @change="getdata1()">
                            <el-option label="全部" value="*"></el-option>
                            <el-option v-for="item in optionsa2" :key="item.value" :label="item.label" :value="item.label"></el-option>
                        </el-select>
                        <el-select v-model="valuea3" placeholder="运营商" style="width: 10%;margin-right: 10px;" @change="getdata1()">
                            <el-option label="全部" value="*"></el-option>
                            <el-option v-for="item in optionsa3" :key="item.value" :label="item.label" :value="item.label"></el-option>
                        </el-select>
                        <el-button-group>
                            <el-button v-show="!showzdyz" @click="today(2)">今天</el-button>
                            <el-button v-show="!showzdyz" @click="yesterday(2)">昨天</el-button>
                            <el-button v-show="!showzdyz" @click="sevendat(2)">近7天</el-button>
                            <el-button v-show="!showzdyz" @click="thirtyday(2)">近30天</el-button>
                            <el-button @click="showzdyzs">自定义<i class="el-icon-date"></i></el-button>
                        </el-button-group>
                        <el-date-picker v-show="showzdyz" style="margin-left:10px;" v-model="val2" type="datetimerange" :picker-options="pickerOptions" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" align="left" @change="gettimes"></el-date-picker>
                        <el-button style="margin-left:10px;" type="primary" @click="seachtu(2)">确定</el-button>
                    </div>
                    <div class="device_form" style>
                        <div id="myChart1" :style="{ height: '607px' }"></div>
                    </div>
                </el-tab-pane>
            </el-tabs>
        </div>
    </div>
</section>
</template>

<script>
import {
    dateToMs,
    getymdtime
} from "../../servers/sevdate";
import fenye2 from "@/components/fenye2";
import {
    accelerate_flow_query_conditions,
    accelerate_flow,
    accelerate_flow_table,
    backsource_flow_query_conditions,
    backsource_flow
} from "../../servers/api";
import echarts from "echarts";
import common from '../../comm/js/util'

export default {
    data() {
        return {
            value1Active: "",
            options1Active: [],
            shoudzyx: false,
            showzdyz: false,
            options1: [],
            options2: [],
            options3: [],
            options4: [],
            optionsa1: [],
            optionsa2: [],
            optionsa3: [],
            optionsa4: [],
            value1: "",
            value2: "",
            value3: "",
            value4: "",
            valuea1: "",
            valuea2: "",
            valuea3: "",
            valuea4: "",
            tablecdn: [],
            activeName: "first",
            pickerOptions: {
                shortcuts: [{
                        text: "昨天",
                        onClick(picker) {
                            const end = new Date(
                                new Date(new Date().toLocaleDateString()).getTime()
                            );
                            const start =
                                new Date(new Date(new Date().toLocaleDateString()).getTime()) -
                                3600 * 1000 * 24 * 1;
                            picker.$emit("pick", [start, end]);
                        }
                    },
                    {
                        text: "今天",
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date(
                                new Date(new Date().toLocaleDateString()).getTime()
                            );
                            picker.$emit("pick", [start, end]);
                        }
                    },
                    {
                        text: "最近一周",
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date(
                                new Date(new Date().toLocaleDateString()).getTime()
                            );
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 6);
                            picker.$emit("pick", [start, end]);
                        }
                    },
                    {
                        text: "最近一个月",
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date(
                                new Date(new Date().toLocaleDateString()).getTime()
                            );
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 29);
                            picker.$emit("pick", [start, end]);
                        }
                    }
                ],
                disabledDate(time) {
                    return time.getTime() > Date.now();
                }
            },
            // value1: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
            val2: [],
            rowHeader: [{
                    prop: "time",
                    label: "节点ID"
                },
                {
                    prop: "totals",
                    label: "使用流量"
                },
                {
                    prop: "online_devices",
                    label: "传输次数"
                },
                {
                    prop: "average_online",
                    label: "日期"
                }
            ],
            rowHeader1: [{
                    prop: "time",
                    label: "节点ID"
                },
                {
                    prop: "totals",
                    label: "存储容量"
                },
                {
                    prop: "online_devices",
                    label: "存储次数"
                },
                {
                    prop: "average_online",
                    label: "日期"
                }
            ],
            tableData: [{
                time: "测试数据1",
                totals: "测试数据1",
                online_devices: "测试数据1",
                average_online: "测试数据1",
                new_percent: "测试数据1"
            }],
            timeUnit: 60,
            starttime: "",
            endtime: "",
            dataFlowArray: [], //图1
            timeArray: [], //图1
            dataFlowArray2: [], //图2
            timeArray2: [], //图2
            pageSize: 10, //每页数量
            pageNo: 1, //当前页码
            total_cnt: 1, //数据总量
            chanid: ""
        };
    },
    filters: {
        settimes(data) {
            var stat = getymdtime(data);
            return stat;
        }
    },
    components: {
        fenye2
    },
    mounted() {
        // if (this.$cookies.get("id")) {
        //     this.chanid = this.$cookies.get("id") * 1;
        // } else {
        //     this.$router.push({
        //         path: "/"
        //     });
        // }
        // if (this.$route.query.urldata) {
        //   console.log(this.$route.query.urldata);
        // }

        this.starttime = new Date(new Date().toLocaleDateString()).getTime() / 1000;
        this.endtime = Date.parse(new Date()) / 1000;

        this.getseachlabel1();
        // this.configure()
    },
    beforeDestroy() {
        if (!this.chart) {
            return;
        }
        this.chart.dispose();
        this.chart = null;
    },
    methods: {
        getdataActive() {

        },
        showzdyzs() {
            this.showzdyz = !this.showzdyz;
        },
        showzdyx() {
            this.shoudzyx = !this.shoudzyx;
        },
        //获取页码
        getpage(pages) {
            this.pageNo = pages;
            this.getbot();
        },
        //获取每页数量
        gettol(pagetol) {
            this.pagesize = pagetol;
            // this.getuserlist();
        },
        getdata() {
            this.gettable1();
        },
        getdata1() {
            this.gettable2();
        },

        /** 请求数据 */
        //加速流量加载查询条件
        getseachlabel1() {
            this.options1 = [];
            this.options2 = [];
            this.options3 = [];
            let params = new Object();
            params.chanId = "*";
            accelerate_flow_query_conditions(params)
                .then(res => {
                    res.data.fileNameSet.forEach((item, index) => {
                        let obj = {};
                        obj.label = item;
                        obj.value = index;
                        this.options1.push(obj);
                    });
                    res.data.regionSet.forEach((item, index) => {
                        let obj = {};
                        obj.label = item;
                        obj.value = index;
                        this.options2.push(obj);
                    });
                    res.data.ispSet.forEach((item, index) => {
                        let obj = {};
                        obj.label = item;
                        obj.value = index;
                        this.options3.push(obj);
                    });
                    res.data.chanIdSet.forEach((item, index) => {
                        let obj = {};
                        obj.label = item;
                        obj.value = index;
                        this.options1Active.push(obj);
                    });
                    this.gettable1();
                })
                .catch(Error => {});
        },
        //请求数据--加速流量条形图
        gettable1() {
            this.dataFlowArray = [];
            this.timeArray = [];
            let params = new Object();
            params.start_ts = this.starttime;
            params.end_ts = this.endtime;
            // params.chanId = this.chanid + "";
            if (this.value1) {
                params.fileName = this.value1;
            } else {
                params.fileName = "*";
            }
            if (this.value2) {
                params.region = this.value2;
            } else {
                params.region = "*";
            }
            if (this.value3) {
                params.isp = this.value3;
            } else {
                params.isp = "*";
            }
            if (this.value3) {
                params.isp = this.value3;
            } else {
                params.isp = "*";
            }

            if (this.value1Active !== "") {
                params.chanId = this.value1Active;
            } else {
                params.chanId = "*";
            }
            // params.time_unit = this.timeUnit;
            console.log(this.endtime,this.starttime)
            params.time_unit=Math.ceil((this.endtime-this.starttime)/60/12)
            accelerate_flow(params)
                .then(res => {
                    this.dataFlowArray = res.data.streamArray;
                    // this.timeArray = res.data.timeArray;
                    res.data.timeArray.forEach((item, index) => {
                        this.timeArray.push(getymdtime(item));
                    });
                    this.getbot();
                    this.drawLine();
                })
                .catch(err => {});
        },
        //请求数据--加速流量表格
        getbot() {
            let params = new Object();
            params.start_ts = this.starttime;
            params.end_ts = this.endtime;
            params.chanId = this.chanid + "";
            if (this.value1) {
                params.fileName = this.value1;
            } else {
                params.fileName = "*";
            }
            if (this.value2) {
                params.region = this.value2;
            } else {
                params.region = "*";
            }
            if (this.value3) {
                params.isp = this.value3;
            } else {
                params.isp = "*";
            }
            if (this.value1Active !== "") {
                params.chanId = this.value1Active;
            } else {
                params.chanId = "*";
            }
            params.pageNo = this.pageNo - 1;
            params.pageSize = this.pageSize;
            accelerate_flow_table(params)
                .then(res => {
                    this.tablecdn = res.data.tableList;
                    this.total_cnt = res.data.totalCnt;
                })
                .catch(err => {});
        },
        //请求数据--回源查询条件
        getseachlabel2() {
            this.optionsa1 = [];
            this.optionsa2 = [];
            this.optionsa3 = [];
            this.options1Active = [];
            let params = new Object();
            if (this.value1Active !== "") {
                params.chanId = this.value1Active;
            } else {
                params.chanId = "*";
            }
            backsource_flow_query_conditions(params)
                .then(res => {
                    res.data.fileNameSet.forEach((item, index) => {
                        let obj = {};
                        obj.label = item;
                        obj.value = index;
                        this.optionsa1.push(obj);
                    });
                    res.data.regionSet.forEach((item, index) => {
                        let obj = {};
                        obj.label = item;
                        obj.value = index;
                        this.optionsa2.push(obj);
                    });
                    res.data.ispSet.forEach((item, index) => {
                        let obj = {};
                        obj.label = item;
                        obj.value = index;
                        this.optionsa3.push(obj);
                    });
                      res.data.chanIdSet.forEach((item, index) => {
                        let obj = {};
                        obj.label = item;
                        obj.value = index;
                        this.options1Active.push(obj);
                    });
                    this.gettable2();
                })
                .catch(err => {});
        },
        gettable2() {
            this.dataFlowArray2 = [];
            this.timeArray2 = [];
            let params = new Object();
            params.start_ts = this.starttime;
            params.end_ts = this.endtime;
            //params.chanId = this.chanid + "";
            if (this.valuea1) {
                params.fileName = this.valuea1;
            } else {
                params.fileName = "*";
            }
            if (this.valuea2) {
                params.region = this.valuea2;
            } else {
                params.region = "*";
            }

            if (this.value3) {
                params.isp = this.value3;
            } else {
                params.isp = "*";
            }
            if (this.value1Active !== "") {
                params.chanId = this.value1Active;
            } else {
                params.chanId = "*";
            }
            // params.time_unit=this.common.timeUnit(this.starttime, this.endtime)
            params.time_unit=Math.ceil((this.endtime-this.starttime)/60/12)
            backsource_flow(params)
                .then(res => {
                    this.dataFlowArray2 = res.data.streamArray;
                    // this.timeArray = res.data.timeArray;
                    res.data.timeArray.forEach((item, index) => {
                        this.timeArray2.push(getymdtime(item));
                    });

                    this.drawLine1();
                })
                .catch(err => {});
        },
        seachtu(data) {
            if (data == 1) {
                this.getseachlabel1();
            } else {
                this.getseachlabel2();
            }
        },
        //今天
        today(data) {
            let times = new Date(new Date().toLocaleDateString()).getTime() / 1000;
            this.starttime = times;
            this.endtime = Date.parse(new Date()) / 1000;
            this.timeUnit = 60;
            if (!data) {
                this.getseachlabel1();
            } else {
                this.getseachlabel2();
            }
        },
        //昨天
        yesterday(data) {
            let times = new Date(new Date().toLocaleDateString()).getTime() / 1000;
            this.starttime = times - 24 * 60 * 60 * 1;
            this.endtime = times;
            this.timeUnit = 60;
            if (!data) {
                this.getseachlabel1();
            } else {
                this.getseachlabel2();
            }
        },
        //七天
        sevendat(data) {
            let times = new Date(new Date().toLocaleDateString()).getTime() / 1000;
            this.starttime = times - 24 * 60 * 60 * 7;
            this.endtime = times;
            this.timeUnit = 60 * 24;
            if (!data) {
                this.getseachlabel1();
            } else {
                this.getseachlabel2();
            }
        },
        //三十天
        thirtyday(data) {
            let times = new Date(new Date().toLocaleDateString()).getTime() / 1000;
            this.starttime = times - 24 * 60 * 60 * 30;
            this.endtime = times;
            this.timeUnit = 60 * 24;
            if (!data) {
                this.getseachlabel1();
            } else {
                this.getseachlabel2();
            }
        },
        //自定义时间
        gettimes(cal) {
            this.starttime = dateToMs(this.val2[0]);
            this.endtime = dateToMs(this.val2[1]);
            if (this.endtime - this.starttime < 21600) {
                this.timeUnit = 60;
            } else if (
                this.endtime - this.starttime >= 21600 &&
                this.endtime - this.starttime < 86400
            ) {
                this.timeUnit = 60;
            } else if (this.endtime - this.starttime >= 86400) {
                this.timeUnit = 60 * 24;
            }
            
        },

        // 表头样式设置
        headClass() {
            return "text-align: center;background:#F3F6FB;";
        },
        // 表格样式设置
        rowClass() {
            return "text-align: center;";
        },
        //选项卡
        handleClick(tab, event) {
            if (tab.index == 0) {
                this.value1 = "";
                this.value2 = "";
                this.value3 = "";
                this.value1Active = "";
                this.timeUnit = 60;
                this.getseachlabel1();
            } else {
                this.valuea1 = "";
                this.valuea2 = "";
                this.valuea3 = "";
                this.value1Active = "";
                this.timeUnit = 60;
                this.getseachlabel2();
            }
        },
        drawLine() {
            // 基于准备好的dom，初始化echarts实例
            let myChart = this.$echarts.init(document.getElementById("myChart"));
            window.onresize = myChart.resize;
            // 绘制图表
            let options = {
                title: {
                    text: "流量"
                },
                grid: {
                    // 间距是 根据x、y轴计算的；假如都是0，x、y轴的label汉字就隐藏掉了。
                    left: "5%", // 默认10%，给24就挺合适的。
                    top: 60, // 默认60
                    right: 35, // 默认10%
                    bottom: 60 // 默认60
                    // width: "100%", // grid 组件的宽度。默认自适应。
                    // height: "100%",
                    // containLabel:true, // grid 区域是否包含坐标轴的刻度标签。(如果true的时候，上下左右可以为0了)
                    // show:true, // 是否显示直角坐标系网格。是否显示grid，grid:show后，下面的一些参数生效。
                    // backgroundColor:'#ccac62',
                    // borderColor:"#000",
                },
                color: "#297AFF",
                tooltip: {
                    trigger: "axis",
                    axisPointer: {
                        type: "cross",
                        label: {
                            backgroundColor: "#6a7985"
                        }
                    }
                },
                xAxis: {
                    data: this.timeArray,
                    
                },
                yAxis: {
                    
                },
                series: [{
                    name: "流量",
                    type: "bar",
                    barWidth: 30, //柱图宽度
                    data: this.dataFlowArray,
                    axisLabel: {
                interval:1
            }
                }]
            };
            myChart.setOption(options);
        },
        drawLine1() {
            // 基于准备好的dom，初始化echarts实例
            let myChart = this.$echarts.init(document.getElementById("myChart1"));
            window.onresize = myChart.resize;
            // 绘制图表
            let options = {
                title: {
                    text: "流量"
                },
                grid: {
                    // 间距是 根据x、y轴计算的；假如都是0，x、y轴的label汉字就隐藏掉了。
                    left: "5%", // 默认10%，给24就挺合适的。
                    top: 60, // 默认60
                    right: 35, // 默认10%
                    bottom: 60 // 默认60
                    // width: "100%", // grid 组件的宽度。默认自适应。
                    // height: "100%",
                    // containLabel:true, // grid 区域是否包含坐标轴的刻度标签。(如果true的时候，上下左右可以为0了)
                    // show:true, // 是否显示直角坐标系网格。是否显示grid，grid:show后，下面的一些参数生效。
                    // backgroundColor:'#ccac62',
                    // borderColor:"#000",
                },
                color: "#297AFF",
                tooltip: {
                    trigger: "axis",
                    axisPointer: {
                        type: "cross",
                        label: {
                            backgroundColor: "#6a7985"
                        }
                    }
                },
                xAxis: {
                    data: this.timeArray2
                },
                yAxis: {},
                series: [{
                    name: "销量",
                    type: "bar",
                    barWidth: 30, //柱图宽度
                    data: this.dataFlowArray2
                }]
            };
            myChart.setOption(options);
        }
    }
};
</script>

<style lang="scss">
.myself-container {
    width: 100%;
    min-width: 1600px;

    .device_form {
        width: auto;
        height: auto;
        margin-top: 20px;
        background: #ffffff;
        padding: 15px 30px;
        box-sizing: border-box;
        box-shadow: 0px 2px 3px 0px rgba(6, 17, 36, 0.14);
        border-radius: 2px;

        .bottom {
            margin-top: 20px;
        }

        .el-form-item__label {
            white-space: nowrap;
        }

        .el-form-item {
            margin-bottom: 0px;
            margin-left: 10px;
        }

        .row_activess {
            margin-top: 20px;
            display: flex;
            justify-content: flex-start;
        }

        .div_show {
            width: auto;
            display: flex;
            height: 40px;
            justify-content: center;
            align-items: center;
            color: #409eff;
            cursor: pointer;
            margin-left: 20px;
        }
    }

    .devide_table {
        padding: 35px;
        height: auto;
        margin-top: 20px;
        background: #ffffff;
        border-radius: 2px;
        box-shadow: 0px 2px 3px 0px rgba(6, 17, 36, 0.14);
        border-radius: 2px;

        .el-table td,
        .el-table th {
            padding: 6px 0px;
        }
    }

    .devide_pageNation {
        width: 100%;
        height: auto;
        // overflow: hidden;
        margin-top: 20px;

        .devide_pageNation_active {
            float: right;
        }
    }
}

.addaccout {
    .el-form--label-left .el-form-item__label {
        text-align: right;
        width: 90px;
    }

    .el-form-item__error {
        margin-left: 80px;
    }
}

